<template>
  <div class="container-item" :style="{ width: getWidth }">
    <div class="container-item-label c-flex-cc">
      <slot name="label">{{ label }}</slot>
    </div>
    <div class="container-item-value c-flex-cc">
      <slot name="value">{{ value }}</slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "BaseInfoItem",
  props: {
    label: {
      type: [String],
      required: true,
    },
    value: {
      type: [String],
      required: true,
    },
    /** 几列，注意：label跟value合起来是一列 */
    column: {
      type: [String, Number],
      default: 4,
      required: false,
    },
  },
  computed: {
    getWidth() {
      return 100 / this.column + "%"
    },
  },
}
</script>

<style scoped>
.container-item {
  /* 需要几个字段就用100%/几得出，比如一行显示4个字段，则为25% */
  box-sizing: border-box;
  font-size: 14px;
  line-height: 1.25em;
  font-weight: 500;
  border-right: 1px solid var(--border-2, #e5e6eb);
  border-bottom: 1px solid var(--border-2, #e5e6eb);
  display: flex;
}
.container-item:last-child {
  flex: 1;
}

.container-item-label {
  padding: 10px;
  border-right: 1px solid var(--border-2, #e5e6eb);
  width: 150px;
  background-color: var(--fill-2, #f2f3f5);
}
.container-item-value {
  padding: 10px;
  flex: 1;
}
</style>
