const dialogMixin = {
  props: {
    title: {
      type: [String],
      default: "",
    },
    width: {
      type: [String],
      default: "50%",
    },
    fullscreen: {
      type: [Boolean],
      default: false,
    },
    top: {
      type: [String],
      default: "15vh",
    },
    modal: {
      type: [Boolean],
      default: true,
    },
    modalAppendToBody: {
      type: [Boolean],
      default: true,
    },
    appendToBody: {
      type: [Boolean],
      default: true,
    },
    lockScroll: {
      type: [Boolean],
      default: true,
    },
    customClass: {
      type: [String],
      default: "",
    },
    closeOnClickModal: {
      type: [Boolean],
      default: true,
    },
    closeOnPressEscape: {
      type: [Boolean],
      default: true,
    },
    showClose: {
      type: [Boolean],
      default: true,
    },
    beforeClose: {
      type: [Function],
    },
    center: {
      type: [Boolean],
      default: false,
    },
    destroyOnClose: {
      type: [Boolean],
      default: false,
    },
    on: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      visible: false,
    }
  },
  watch: {
    visible(value) {
      // 动画结束后销毁实例
      if (value === false) {
        setTimeout(() => {
          this.$destroy()
          if (this?.$el?.parentNode) {
            this.$el.parentNode.removeChild(this.$el)
          }
        }, 400)
      }
    },
  },
  mounted() {
    document.body.appendChild(this.$el)
    this.visible = true
  },
  methods: {
    // 关闭对话框
    close(refresh) {
      this.visible = false
      if (refresh) {
        this?.on?.refresh(true) // 刷新
      }
    },
  },
}

export default {
  install(Vue, config = {}) {
    Vue.prototype[`$${config.prefix}Dialog`] = (component, props) => {
      component.default && (component = component.default)
      const mixins = component.mixins || []
      mixins.push(dialogMixin) // 实现自动打开，动态了混入生命周期函数和销毁操作
      component.mixins = mixins
      const Dialog = Vue.extend(component)
      const dialog = new Dialog({
        propsData: props || {},
      })
      dialog.$mount()
    }
  },
}

