const permissions = {
  state: {
    permissions: [-1], // 权限，-1表示管理员权限，拥有所有权限
  },
  mutations: {
    /** 设置管理员权限 */
    setAdminPermissions(state) {
      state.permissions = [-1]
    },
    /** 添加删除权限 */
    addDeletePermissions(state) {
      if (!state.permissions.includes("hasDelete")) {
        state.permissions = state.permissions.concat(["hasDelete"])
      }
    },
    /** 清空权限 */
    clearPermissions(state) {
      state.permissions = []
    },
  },
  actions: {},
}

export default permissions
